.catalog {
  padding: 50px $calendarLeftPadding;
  display: flex;
  flex-wrap: wrap;
  min-width: getMaxWidth();
  max-width: getMaxWidth();
  margin: 0 auto;
  
  &__card {
    width: 200px;
    padding: 32px 16px 16px;
    background-color: #fff;
    border: 1px solid #f3f3f3;
    border-radius: 5px;
  }

}
